import {
  JobActions,
  ParseDroppedFiles,
  ReceiveCreateDocuments,
  ReceiveCreateJobs,
  ReceiveDroppedFiles,
  ReceiveGetJobs,
  ReceiveGetJobtypes,
  RequestCreateDocuments,
  RequestCreateJobs,
  RequestGetJobs,
} from "@app/actions/job";
import { IKindHandlers, Runner } from "@app/reducers/handlers";
import { ICreateJobErrors, IJobState } from "@app/types/job";
import { FetchLevel } from "@app/types/main";
import { getJobMap, IDocument, IErrors, IJob } from "@ilabs/core";

const handlers: IKindHandlers<IJobState, JobActions> = {
  documents: {
    create: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveCreateDocuments>): IJobState => {
        const documents = action.payload
          .filter(v => v.result.document !== null)
          .map<IDocument>(v => v.result.document!);

        const createResultDemoFileIds = action.payload.map(v => v.droppedFileId);
        const createRequests = state.documents.createRequests.filter(
          v => !createResultDemoFileIds.includes(v.droppedFileId),
        );

        return {
          ...state,
          documents: {
            ...state.documents,
            createRequests,
            createResults: [...state.documents.createResults, ...action.payload],
            list: {
              ...state.documents.list,
              data: [...state.documents.list.data, ...documents],
            },
          },
        };
      },
      request: (state: IJobState, action: ReturnType<typeof RequestCreateDocuments>): IJobState => {
        const createRequestDemoFileIds = action.payload.map(v => v.droppedFileId);
        const files = state.documents.dropped.files.filter(
          v => !createRequestDemoFileIds.includes(v.id),
        );

        return {
          ...state,
          documents: {
            ...state.documents,
            createRequests: [...state.documents.createRequests, ...action.payload],
            dropped: {
              ...state.documents.dropped,
              files,
            },
          },
        };
      },
    },
  },
  files: {
    dropped: {
      parse: (state: IJobState, action: ReturnType<typeof ParseDroppedFiles>): IJobState => {
        return {
          ...state,
          documents: {
            ...state.documents,
            dropped: {
              ...state.documents.dropped,
              errors: {},
              files: [...state.documents.dropped.files, ...action.payload],
            },
          },
        };
      },
      receive: (state: IJobState, action: ReturnType<typeof ReceiveDroppedFiles>): IJobState => {
        const errors = action.payload
          .filter(v => v.error !== null)
          .reduce<IErrors>((out, v) => {
            return { ...out, [v.file.name]: v.error! };
          }, {});
        if (Object.keys(errors).length > 0) {
          return {
            ...state,
            documents: {
              ...state.documents,
              dropped: { ...state.documents.dropped, errors },
            },
          };
        }

        return {
          ...state,
          documents: {
            ...state.documents,
            dropped: { ...state.documents.dropped, errors: {} },
          },
        };
      },
    },
  },
  jobs: {
    create: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveCreateJobs>): IJobState => {
        const jobs = action.payload
          .filter(v => v.result.response !== null)
          .map<IJob>(v => v.result.response!);
        const errors = action.payload
          .filter(v => v.result.errors !== null)
          .map<ICreateJobErrors>(v => {
            return { input: v.result.input, errors: v.result.errors! };
          });
        const createResultDroppedFileIds = action.payload.map(v => v.droppedFileId);
        const createJobRequests = state.jobs.createRequests.filter(
          v => !createResultDroppedFileIds.includes(v.droppedFileId),
        );

        return {
          ...state,
          jobs: {
            ...state.jobs,
            createRequests: createJobRequests,
            createResults: [...state.jobs.createResults, ...action.payload],
            errors,
            list: {
              ...state.jobs.list,
              data: { ...state.jobs.list.data, ...getJobMap(jobs) },
            },
          },
        };
      },
      request: (state: IJobState, action: ReturnType<typeof RequestCreateJobs>): IJobState => {
        const createJobRequestDroppedFileIds = action.payload.map(v => v.droppedFileId);
        const createDocumentResults = state.documents.createResults.filter(
          v => !createJobRequestDroppedFileIds.includes(v.droppedFileId),
        );

        return {
          ...state,
          documents: {
            ...state.documents,
            createResults: createDocumentResults,
          },
          jobs: {
            ...state.jobs,
            createRequests: [...state.jobs.createRequests, ...action.payload],
            errors: [],
          },
        };
      },
    },
    get: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveGetJobs>): IJobState => {
        return {
          ...state,
          jobs: {
            ...state.jobs,
            list: {
              ...state.jobs.list,
              data: {
                ...state.jobs.list.data,
                ...getJobMap(action.payload),
              },
              fetchLevel: FetchLevel.success,
            },
          },
        };
      },
      request: (state: IJobState, action: ReturnType<typeof RequestGetJobs>): IJobState => {
        const fetchLevel = !action.payload ? FetchLevel.fetching : FetchLevel.refetching;

        return {
          ...state,
          jobs: {
            ...state.jobs,
            list: {
              ...state.jobs.list,
              fetchLevel,
            },
          },
        };
      },
    },
  },
  jobtypes: {
    get: {
      receive: (state: IJobState, action: ReturnType<typeof ReceiveGetJobtypes>): IJobState => {
        return {
          ...state,
          jobTypes: { ...state.jobTypes, fetchLevel: FetchLevel.success, data: action.payload },
        };
      },
      request: (state: IJobState): IJobState => {
        return {
          ...state,
          jobTypes: { ...state.jobTypes, fetchLevel: FetchLevel.fetching },
        };
      },
    },
  },
};

export const run: Runner<IJobState, JobActions> = (
  state: IJobState,
  action: JobActions,
): IJobState => {
  const [kind, verb, task] = action.type
    .split("_")
    .reverse()
    .map(v => v.toLowerCase());
  if (!(kind in handlers)) {
    return state;
  }
  const kindHandlers = handlers[kind];

  if (!(verb in kindHandlers)) {
    return state;
  }
  const verbHandlers = kindHandlers[verb];

  if (!(task in verbHandlers)) {
    return state;
  }
  const taskHandler = verbHandlers[task];

  return taskHandler(state, action);
};



