import queryString from "query-string";

import { IDemoFile, IDroppedFile } from "@app/types/job";

export const apiEndpoint = "https://bb.innodatalabs.com";

export interface IGatherOptions<T> {
  headers?: Headers;
  body?: T;
  method?: string;
  url: string;
}

export interface IGatherQueryOptions<Q> {
  headers?: Headers;
  query?: Q;
  method?: string;
  url: string;
}

export interface IGatherResult<T> {
  response: Response;
  body: T | null;
  status: number;
}

export const gather = async <T, A>(opts: IGatherOptions<T>): Promise<IGatherResult<A>> => {
  const body: string | ArrayBuffer | null = (() => {
    if (typeof opts.body === "undefined") {
      return null;
    }

    if (opts.body instanceof ArrayBuffer) {
      return opts.body;
    }

    return JSON.stringify(opts.body);
  })();
  const method = typeof opts.method === "undefined" ? "GET" : opts.method;
  const headers: Headers = (() => {
    if (typeof opts.headers === "undefined") {
      return new Headers({ "content-type": "application/json" });
    }

    return opts.headers;
  })();

  const response = await fetch(opts.url, {
    body,
    headers,
    method,
  });

  return handleResponse(response);
};

export const gatherWithQuery = async <Q, A>(
  opts: IGatherQueryOptions<Q>,
): Promise<IGatherResult<A>> => {
  const query =
    typeof opts.query === "undefined"
      ? null
      : queryString.stringify(opts.query as any, { arrayFormat: "index" });
  const method = typeof opts.method === "undefined" ? "GET" : opts.method;
  const headers: Headers = (() => {
    if (typeof opts.headers === "undefined") {
      return new Headers({ "content-type": "application/json" });
    }

    return opts.headers;
  })();

  const url = (() => {
    if (query === null) {
      return opts.url;
    }

    return `${opts.url}?${query}`;
  })();

  const response = await fetch(url, {
    headers,
    method,
  });

  return handleResponse(response);
};

const handleResponse = async <A>(response: Response): Promise<IGatherResult<A>> => {
  const responseBody: A | null = await (async () => {
    const responseText = await response.text();
    if (responseText.length === 0) {
      return null;
    }

    const contentType = response.headers.get("content-type");
    if (contentType === null) {
      return null;
    }

    if (!/^application\/json/.test(contentType)) {
      return null;
    }

    return JSON.parse(responseText);
  })();

  return {
    body: responseBody,
    response,
    status: response.status,
  };
};

export type FiletypeWhitelist = string[];

export type FileReaderFunc = (
  opts?: IFileReaderOptions,
) => (demoFile: IDemoFile) => Promise<IDroppedFile>;

export interface IFileReaderOptions {
  mimetypeWhitelist?: FiletypeWhitelist;
  sizeUpperLimit?: number; // bytes
}

export const ValidateMimetype = (settings: IFileReaderOptions, file: File) => {
  if (typeof settings.mimetypeWhitelist === "undefined") {
    return null;
  }

  if (settings.mimetypeWhitelist.length === 0) {
    return null;
  }

  if (settings.mimetypeWhitelist.indexOf(file.type) === -1) {
    return "file type not in whitelist";
  }

  return null;
};

export const ValidateFilesize = (settings: IFileReaderOptions, file: File) => {
  if (typeof settings.sizeUpperLimit === "undefined") {
    return null;
  }

  if (settings.sizeUpperLimit === 0) {
    return null;
  }

  if (file.size > settings.sizeUpperLimit) {
    return "file type not in whitelist";
  }

  return null;
};

export const generateFileReader: FileReaderFunc = (opts?: IFileReaderOptions) => {
  const settings: IFileReaderOptions = typeof opts === "undefined" ? {} : opts;

  return async (demoFile: IDemoFile) => {
    const result = [ValidateMimetype, ValidateFilesize].reduce<string | null>(
      (out, validateFunc) => {
        if (out !== null) {
          return out;
        }

        return validateFunc(settings, demoFile.file);
      },
      null,
    );
    if (result !== null) {
      const droppedFile: IDroppedFile = {
        droppedFileId: demoFile.id,
        error: result,
        file: { name: demoFile.file.name, data: null },
      };

      return droppedFile;
    }

    return readFile(demoFile);
  };
};

export const readFile = async (demoFile: IDemoFile): Promise<IDroppedFile> => {
  return new Promise<IDroppedFile>(resolve => {
    const reader = new FileReader();
    reader.onabort = () =>
      resolve({
        droppedFileId: demoFile.id,
        error: "file reading was rejected",
        file: { name: demoFile.file.name, data: null },
      });
    reader.onerror = () =>
      resolve({
        droppedFileId: demoFile.id,
        error: "file reading has failed",
        file: { name: demoFile.file.name, data: null },
      });
    reader.onload = () =>
      resolve({
        droppedFileId: demoFile.id,
        error: null,
        file: { data: reader.result! as ArrayBuffer, name: demoFile.file.name },
      });
    reader.readAsArrayBuffer(demoFile.file);
  });
};



