import React, { useContext } from "react";

import { JobContext } from "@app/context-containers";
import {
  ICreateJobErrors,
  IDemoCreateDocumentRequest,
  IDemoCreateDocumentResult,
  IDemoCreateJobRequest,
  IDemoFile,
} from "@app/types/job";
import { IDocument, IErrors, IJobContentRequest } from "@ilabs/core";

// dropped-files
const renderDroppedFile = (demoFile: IDemoFile, i: number, offset: number) => {
  const key = i + offset;

  return (
    <tr key={key}>
      <td className="first-column">{key + 1}.</td>
      <td className="second-column">
        {demoFile.file.name} ({(demoFile.file.size / 1000 / 1000).toFixed(2)}MB)
      </td>
      <td className="third-column">
        <span className="badge badge-warning">PARSING DOCUMENT</span>
      </td>
      <td className="fourth-column">
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={0}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "0%" }}
          >
            0%
          </div>
        </div>
      </td>
      <td className="sixth-column">
        <a href="#">×</a>
      </td>
    </tr>
  );
};

const renderDroppedFiles = (files: IDemoFile[], offset: number) => {
  if (files.length === 0) {
    return null;
  }

  return files.map((v, i) => renderDroppedFile(v, i, offset));
};

// drop-errors
const renderDropError = (filename: string, value: string, i: number, offset: number) => {
  const key = i + offset;

  return (
    <tr key={key}>
      <td className="first-column">{key + 1}.</td>
      <td className="second-column">
        <strong>{filename}</strong>: {value}
      </td>
      <td className="third-column">
        <span className="badge badge-danger">FAILED PARSING DOCUMENT</span>
      </td>
      <td className="fourth-column">
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={0}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "0%" }}
          >
            0%
          </div>
        </div>
      </td>
      <td className="sixth-column">
        <a href="#">×</a>
      </td>
    </tr>
  );
};

const renderDropErrors = (errors: IErrors, offset: number) => {
  if (Object.keys(errors).length === 0) {
    return null;
  }

  return Object.keys(errors).map((k, i) => renderDropError(k, errors[k], i, offset));
};

// create-document requests
const renderCreateDocumentRequest = (
  request: IDemoCreateDocumentRequest,
  i: number,
  offset: number,
) => {
  const key = i + offset;

  return (
    <tr key={key}>
      <td className="first-column">{key + 1}.</td>
      <td className="second-column">{request.request.name}</td>
      <td className="third-column">
        <span className="badge badge-warning">UPLOADING DOCUMENT</span>
      </td>
      <td className="fourth-column">
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={0}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "0%" }}
          >
            0%
          </div>
        </div>
      </td>
      <td className="sixth-column">
        <a href="#">×</a>
      </td>
    </tr>
  );
};

const renderCreateDocumentRequests = (requests: IDemoCreateDocumentRequest[], offset: number) => {
  if (requests.length === 0) {
    return null;
  }

  return requests.map((v, i) => renderCreateDocumentRequest(v, i, offset));
};

// create-document results
const renderCreateDocumentResultError = (key: string, value: string, i: number) => (
  <li key={i}>{value}</li>
);

const renderCreateDocumentResultErrors = (errors: IErrors) => {
  return (
    <ul>{Object.keys(errors).map((k, i) => renderCreateDocumentResultError(k, errors[k], i))}</ul>
  );
};

const renderCreateDocumentResultDocument = (
  result: IDemoCreateDocumentResult,
  i: number,
  offset: number,
) => {
  const key = i + offset;

  return (
    <tr key={key}>
      <td className="first-column">{key + 1}.</td>
      <td className="second-column">{result.result.document!.name}</td>
      <td className="third-column">
        <span className="badge badge-warning">UPLOADED DOCUMENT</span>
      </td>
      <td className="fourth-column">
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={0}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "0%" }}
          >
            0%
          </div>
        </div>
      </td>
      <td className="sixth-column">
        <a href="#">×</a>
      </td>
    </tr>
  );
};

const renderCreateDocumentResult = (
  result: IDemoCreateDocumentResult,
  i: number,
  offset: number,
) => {
  const key = i + offset;

  if (result.result.errors !== null) {
    return (
      <li key={key}>
        There were errors creating document <strong>{result.result.name}</strong>:
        {renderCreateDocumentResultErrors(result.result.errors)}
      </li>
    );
  }

  if (result.result.document !== null) {
    return renderCreateDocumentResultDocument(result, i, offset);
  }

  return null;
};

const renderCreateDocumentResults = (results: IDemoCreateDocumentResult[], offset: number) => {
  if (results.length === 0) {
    return null;
  }

  return results.map((v, i) => renderCreateDocumentResult(v, i, offset));
};

// create-job requests
const renderCreateJobRequestInput = (
  input: IJobContentRequest | undefined,
  document: IDocument | null,
) => {
  if (document && document.name) {
    return document.name;
  }
  if (input && input.name) {
    return input.name;
  }
  return null;
};

const renderCreateJobRequest = (
  request: IDemoCreateJobRequest,
  i: number,
  offset: number,
  documents: IDocument[],
) => {
  const key = i + offset;

  const input = request.request.contents.find(c => c.role === "input");

  const document: IDocument | null = documents.reduce<IDocument | null>((out, v) => {
    if (out !== null) {
      return out;
    }

    if (input && v.contents_uri === input.uri) {
      return v;
    }

    return null;
  }, null);

  return (
    <tr key={key}>
      <td className="first-column">{key + 1}.</td>
      <td className="second-column">{renderCreateJobRequestInput(input, document)}</td>
      <td className="third-column">
        <span className="badge badge-warning">CREATING JOB</span>
      </td>
      <td className="fourth-column">
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={0}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "0%" }}
          >
            0%
          </div>
        </div>
      </td>
      <td className="sixth-column">
        <a href="#">×</a>
      </td>
    </tr>
  );
};

const renderCreateJobRequests = (
  requests: IDemoCreateJobRequest[],
  offset: number,
  documents: IDocument[],
) => {
  if (requests.length === 0) {
    return null;
  }

  return requests.map((v, i) => renderCreateJobRequest(v, i, offset, documents));
};

// create-job errors
const renderCreateJobError = (
  error: ICreateJobErrors,
  i: number,
  offset: number,
  documents: IDocument[],
) => {
  const key = i + offset;

  const document: IDocument | null = documents.reduce<IDocument | null>((out, v) => {
    if (out !== null) {
      return out;
    }

    if (v.contents_uri === error.input.uri) {
      return v;
    }

    return null;
  }, null);

  return (
    <tr key={key}>
      <td className="first-column">{key + 1}.</td>
      <td className="second-column">
        {`${renderCreateJobRequestInput(error.input, document)}: ${Object.keys(error.errors)
          .map(v => error.errors[v])
          .join(", ")}`}
      </td>
      <td className="third-column">
        <span className="badge badge-danger">FAILED CREATING JOB</span>
      </td>
      <td className="fourth-column">
        <div className="progress">
          <div
            className="progress-bar"
            role="progressbar"
            aria-valuenow={0}
            aria-valuemin={0}
            aria-valuemax={100}
            style={{ width: "0%" }}
          >
            0%
          </div>
        </div>
      </td>
      <td className="sixth-column">
        <a href="#">×</a>
      </td>
    </tr>
  );
};

const renderCreateJobErrors = (
  errorsList: ICreateJobErrors[],
  offset: number,
  documents: IDocument[],
) => {
  if (errorsList.length === 0) {
    return null;
  }

  return errorsList.map((v, i) => renderCreateJobError(v, i, offset, documents));
};

export function NonJobs() {
  // job-context
  const {
    state: {
      jobs: {
        createRequests: createJobRequests,
        list: { data: jobs },
        errors: createJobErrors,
      },
      documents: {
        dropped,
        createRequests: createDocumentRequests,
        createResults: createDocumentResults,
        list: { data: documents },
      },
    },
  } = useContext(JobContext)!;

  const countOffset = Object.keys(jobs).length;

  return (
    <>
      {renderDroppedFiles(dropped.files, countOffset)}
      {renderDropErrors(dropped.errors, countOffset)}
      {renderCreateDocumentRequests(createDocumentRequests, countOffset)}
      {renderCreateDocumentResults(createDocumentResults, countOffset)}
      {renderCreateJobRequests(createJobRequests, countOffset, documents)}
      {renderCreateJobErrors(createJobErrors, countOffset, documents)}
    </>
  );
}



