import { IFileReaderOptions } from "@app/api";
import { ICreateDocumentRequest, ICreateDocumentResult } from "@app/api/v1/documents";
import { FetchLevel, IApiData } from "@app/types/main";
import {
  ICreateJobRequest,
  IDocument,
  IErrors,
  IJob,
  IJobContentRequest,
  IJobMap,
  IJobType,
  UUID,
} from "@ilabs/core";
import { ICreateJobResult } from "@ilabs/ui";

export interface IDroppedFile {
  error: string | null;
  droppedFileId: UUID;
  file: {
    name: string;
    data: ArrayBuffer | null;
  };
}

export interface IDemoFile {
  id: UUID;
  file: File;
}

export interface ICreateJobErrors {
  errors: IErrors;
  input: IJobContentRequest;
}

export interface IDemoCreateDocumentRequest {
  droppedFileId: UUID;
  request: ICreateDocumentRequest;
}

export interface IDemoCreateDocumentResult {
  droppedFileId: UUID;
  result: ICreateDocumentResult;
}

export interface IDemoCreateJobRequest {
  droppedFileId: UUID;
  request: ICreateJobRequest;
}

export interface IDemoCreateJobResult {
  droppedFileId: UUID;
  result: ICreateJobResult;
}

export interface IJobState {
  createJobs: IApiData<IJob[]>;
  jobs: {
    errors: ICreateJobErrors[];
    createRequests: IDemoCreateJobRequest[];
    createResults: IDemoCreateJobResult[];
    list: IApiData<IJobMap>;
  };
  jobTypes: IApiData<IJobType[]>;
  fileReaderOptions?: IFileReaderOptions;
  currentJobType: IJobType | null;
  documents: {
    dropped: {
      files: IDemoFile[];
      errors: IErrors;
    };
    createRequests: IDemoCreateDocumentRequest[];
    createResults: IDemoCreateDocumentResult[];
    list: IApiData<IDocument[]>;
  };
}

export const defaultJobState: IJobState = {
  createJobs: {
    data: [],
    errors: {},
    fetchLevel: FetchLevel.initial,
  },
  currentJobType: null,
  documents: {
    createRequests: [],
    createResults: [],
    dropped: {
      errors: {},
      files: [],
    },
    list: {
      data: [],
      errors: {},
      fetchLevel: FetchLevel.initial,
    },
  },
  fileReaderOptions: {
    mimetypeWhitelist: ["application/pdf", "application/xml", "text/xml"],
    sizeUpperLimit: 10 * 1000 * 1000, // 10MB
  },
  jobTypes: {
    data: [],
    errors: {},
    fetchLevel: FetchLevel.initial,
  },
  jobs: {
    createRequests: [],
    createResults: [],
    errors: [],
    list: {
      data: {},
      errors: {},
      fetchLevel: FetchLevel.initial,
    },
  },
};



