import { Dispatch } from "react";

import { ActionsUnion, createAction } from "@app/actions/helpers";
import { generateFileReader } from "@app/api";
import { createDocument, getDocuments } from "@app/api/v1/documents";
import { createJob, getJob, getJobs } from "@app/api/v1/jobs";
import { getJobTypes } from "@app/api/v1/jobTypes";
import {
  IDemoCreateDocumentRequest,
  IDemoCreateDocumentResult,
  IDemoCreateJobRequest,
  IDemoCreateJobResult,
  IDemoFile,
  IDroppedFile,
  IJobState,
} from "@app/types/job";
import { IDocument, IJob, IJobType, UUID } from "@ilabs/core";

export const REQUEST_GET_JOBTYPES = "REQUEST_GET_JOBTYPES";
export const RECEIVE_GET_JOBTYPES = "RECEIVE_GET_JOBTYPES";
export const RequestGetJobtypes = () => createAction(REQUEST_GET_JOBTYPES);
export const ReceiveGetJobtypes = (payload: IJobType[]) =>
  createAction(RECEIVE_GET_JOBTYPES, payload);

export const REQUEST_GET_JOBS = "REQUEST_GET_JOBS";
export const RECEIVE_GET_JOBS = "RECEIVE_GET_JOBS";
export const RequestGetJobs = (payload?: boolean) => createAction(REQUEST_GET_JOBS, payload);
export const ReceiveGetJobs = (payload: IJob[]) => createAction(RECEIVE_GET_JOBS, payload);

export const REQUEST_GET_DOCUMENTS = "REQUEST_GET_DOCUMENTS";
export const RECEIVE_GET_DOCUMENTS = "RECEIVE_GET_DOCUMENTS";
export const RequestGetDocuments = (payload?: boolean) =>
  createAction(REQUEST_GET_DOCUMENTS, payload);
export const ReceiveGetDocuments = (payload: IDocument[]) =>
  createAction(RECEIVE_GET_DOCUMENTS, payload);

export const PARSE_DROPPED_FILES = "PARSE_DROPPED_FILES";
export const RECEIVE_DROPPED_FILES = "RECEIVE_DROPPED_FILES";
export const ParseDroppedFiles = (payload: IDemoFile[]) =>
  createAction(PARSE_DROPPED_FILES, payload);
export const ReceiveDroppedFiles = (payload: IDroppedFile[]) =>
  createAction(RECEIVE_DROPPED_FILES, payload);

export const REQUEST_CREATE_DOCUMENTS = "REQUEST_CREATE_DOCUMENTS";
export const RECEIVE_CREATE_DOCUMENTS = "RECEIVE_CREATE_DOCUMENTS";
export const RequestCreateDocuments = (payload: IDemoCreateDocumentRequest[]) =>
  createAction(REQUEST_CREATE_DOCUMENTS, payload);
export const ReceiveCreateDocuments = (payload: IDemoCreateDocumentResult[]) =>
  createAction(RECEIVE_CREATE_DOCUMENTS, payload);

export const CHANGE_JOBTYPE = "CHANGE_JOBTYPE";
export const ChangeJobtype = (payload: IJobType) => createAction(CHANGE_JOBTYPE, payload);

export const REQUEST_CREATE_JOBS = "REQUEST_CREATE_JOBS";
export const RECEIVE_CREATE_JOBS = "RECEIVE_CREATE_JOBS";
export const RequestCreateJobs = (payload: IDemoCreateJobRequest[]) =>
  createAction(REQUEST_CREATE_JOBS, payload);
export const ReceiveCreateJobs = (payload: IDemoCreateJobResult[]) =>
  createAction(RECEIVE_CREATE_JOBS, payload);

export const JobActions = {
  ChangeJobtype,
  ParseDroppedFiles,
  ReceiveCreateDocuments,
  ReceiveCreateJobs,
  ReceiveDroppedFiles,
  ReceiveGetDocuments,
  ReceiveGetJobs,
  ReceiveGetJobtypes,
  RequestCreateDocuments,
  RequestCreateJobs,
  RequestGetDocuments,
  RequestGetJobs,
  RequestGetJobtypes,
};

export type JobActions = ActionsUnion<typeof JobActions>;

export const useActions = (state: IJobState, dispatch: Dispatch<JobActions>) => {
  const fileReader = generateFileReader(state.fileReaderOptions);

  return {
    DropFiles: async (authKey: string, files: IDemoFile[], jobType: IJobType) => {
      // parsing the files dropped
      dispatch(ParseDroppedFiles(files));

      const parsedFiles = await Promise.all(files.map(fileReader));
      dispatch(ReceiveDroppedFiles(parsedFiles));

      // producing create-document requests
      const createDocumentRequests = parsedFiles
        .filter(v => v.file.data !== null)
        .map<IDemoCreateDocumentRequest>(v => {
          return {
            droppedFileId: v.droppedFileId,
            request: { authKey, name: v.file.name, body: v.file.data! },
          };
        });
      if (createDocumentRequests.length === 0) {
        return;
      }

      dispatch(RequestCreateDocuments(createDocumentRequests));

      const receivedDocuments = await Promise.all(createDocumentRequests.map(createDocument));
      dispatch(ReceiveCreateDocuments(receivedDocuments));

      // producing create-job requests
      const createJobRequests = receivedDocuments
        .filter(v => v.result.errors === null)
        .map<IDemoCreateJobRequest>(v => {
          const request = {
            droppedFileId: v.droppedFileId,
            request: {
              collaboration: {
                teams: [
                  {
                    name: "iLabs Developers",
                    steps: ["*"],
                  },
                ],
                users: [],
              },
              contents: [
                {
                  name: v.result.document!.name,
                  order: 1,
                  readonly: true,
                  role: "input",
                  size: v.result.document!.size,
                  type: v.result.document!.type,
                  uri: v.result.document!.contents_uri,
                },
                {
                  name: "Privacy Policy Taxonomy",
                  order: 1,
                  readonly: true,
                  role: "mapping-taxonomy-json",
                  size: 7462,
                  type: "application/json",
                  uri:
                    "https://storage.googleapis.com/goldengate.innodatalabs.com/public/privacy-policy-taxonomy.json",
                },
              ],
              high_confidence_threshold: 0.92,
              high_resolution_image_height: 1024,
              mapping: {
                high_confidence_threshold: 1.01,
              },
              type: jobType.id,
              zoning: {
                high_confidence_threshold: 0.95,
                high_resolution_image_height: 1024,
              },
            },
          };
          if (jobType.id === "zoning") {
            request.request.mapping.high_confidence_threshold = -1;
            request.request.zoning.high_confidence_threshold = 1.01;
          }
          return request;
        });
      if (createJobRequests.length === 0) {
        return;
      }

      dispatch(RequestCreateJobs(createJobRequests));

      const receivedJobs = await Promise.all(createJobRequests.map(v => createJob(authKey, v)));
      dispatch(ReceiveCreateJobs(receivedJobs));
    },
    FetchGetDocuments: async (authKey: string, refetching?: boolean) => {
      dispatch(RequestGetDocuments(refetching));
      dispatch(ReceiveGetDocuments(await getDocuments({ authKey })));
    },
    FetchGetJobs: async (authKey: string, ids: UUID[], refetching?: boolean) => {
      dispatch(RequestGetJobs(refetching));

      if (ids.length === 0) {
        dispatch(ReceiveGetJobs(await getJobs({ authKey })));
      } else {
        const fetchedJobs = await Promise.all(
          ids.map<Promise<IJob>>(v => getJob({ authKey, id: v })),
        );
        dispatch(ReceiveGetJobs(fetchedJobs));
      }
    },
    FetchGetJobtypes: async () => {
      dispatch(RequestGetJobtypes());
      dispatch(ReceiveGetJobtypes(await getJobTypes()));
    },
    state,
  };
};



