import { IDemoCreateDocumentRequest, IDemoCreateDocumentResult } from "@app/types/job";
import * as HTTPStatus from "http-status";

import { apiEndpointV1, request, requestWithQuery } from "@app/api/v1/index";
import {
  IApiErrorResponse,
  IApiListParams,
  IAuthenticatedRequest,
  IDocument,
  IErrors,
  IGetDocuments,
} from "@ilabs/core";

export interface ICreateDocumentRequest extends IAuthenticatedRequest {
  name: string;
  body: ArrayBuffer;
}

export interface ICreateDocumentResult {
  name: string;
  document: IDocument | null;
  errors: IErrors | null;
}

export const createDocument = async (
  req: IDemoCreateDocumentRequest,
): Promise<IDemoCreateDocumentResult> => {
  const res = await request<ArrayBuffer, IDocument | IApiErrorResponse>({
    body: req.request.body,
    headers: new Headers({
      authorization: `Basic ${req.request.authKey}`,
      "content-type": "application/octet-stream",
      "x-name": req.request.name,
    }),
    method: "POST",
    url: `${apiEndpointV1}/documents`,
  });

  switch (res.status) {
    case HTTPStatus.UNAUTHORIZED:
    case HTTPStatus.BAD_REQUEST:
      return {
        droppedFileId: req.droppedFileId,
        result: {
          document: null,
          errors: { error: (res.body!.response as IApiErrorResponse)!.message },
          name: req.request.name,
        },
      };
    case HTTPStatus.CREATED:
      const data = res.body!.response as IDocument;

      return {
        droppedFileId: req.droppedFileId,
        result: {
          document: data,
          errors: null,
          name: req.request.name,
        },
      };
    default:
      return {
        droppedFileId: req.droppedFileId,
        result: {
          document: null,
          errors: { error: "Unknown response code" },
          name: req.request.name,
        },
      };
  }
};

export interface IGetDocumentsRequest {
  query?: IApiListParams;
  authKey: string;
}

export const getDocuments = async (req: IGetDocumentsRequest): Promise<IDocument[]> => {
  const res = await requestWithQuery<IApiListParams, IGetDocuments>({
    headers: new Headers({
      authorization: `Basic ${req.authKey}`,
      "content-type": "application/json",
    }),
    method: "GET",
    query: req.query,
    url: `${apiEndpointV1}/documents`,
  });

  return res.body!.response.list;
};



