import React, { createContext, Dispatch, useReducer } from "react";

import { useActions, UserActions } from "@app/actions/user";
import { user } from "@app/reducers";
import { defaultUserState, IUserState } from "@app/types/user";

export interface IUserContext {
  actions: ReturnType<typeof useActions>;
  dispatch: Dispatch<UserActions>;
  state: IUserState;
}

export const UserContext = createContext<IUserContext | null>(null);

interface IOwnProps {
  children: React.ReactChild;
}

type IProps = Readonly<IOwnProps>;

export function UserProvider({ children }: IProps) {
  const [state, dispatch] = useReducer(user, defaultUserState);
  const actions = useActions(state, dispatch);

  return (
    <UserContext.Provider value={{ state, dispatch, actions }}>{children}</UserContext.Provider>
  );
}



