import { IDemoCreateJobRequest, IDemoCreateJobResult } from "@app/types/job";
import * as HTTPStatus from "http-status";

import { apiEndpointV1, request, requestWithQuery } from "@app/api/v1/index";
import {
  CreateJobResponse,
  IApiErrorResponse,
  IApiListParams,
  ICreateJobRequest,
  IGetJobs,
  IJob,
  UUID,
} from "@ilabs/core";

export const createJob = async (
  authKey: string,
  req: IDemoCreateJobRequest,
): Promise<IDemoCreateJobResult> => {
  const res = await request<ICreateJobRequest, CreateJobResponse | IApiErrorResponse>({
    body: req.request,
    headers: new Headers({
      authorization: `Basic ${authKey}`,
      "content-type": "application/json",
    }),
    method: "POST",
    url: `${apiEndpointV1}/jobs`,
  });

  const input = req.request.contents.find(c => c.role === "input");
  if (!input) {
    throw new Error("Job request missing input document.");
  }

  switch (res.status) {
    case HTTPStatus.UNAUTHORIZED:
    case HTTPStatus.BAD_REQUEST:
    case HTTPStatus.INTERNAL_SERVER_ERROR:
      return {
        droppedFileId: req.droppedFileId,
        result: {
          errors: { error: (res.body!.response as IApiErrorResponse)!.message },
          input,
          response: null,
        },
      };
    case HTTPStatus.CREATED:
      return {
        droppedFileId: req.droppedFileId,
        result: {
          errors: null,
          input,
          response: res.body!.response as CreateJobResponse,
        },
      };
    default:
      return {
        droppedFileId: req.droppedFileId,
        result: {
          errors: { error: "Unknown response code" },
          input,
          response: null,
        },
      };
  }
};

export interface IGetJobsRequest {
  query?: IApiListParams;
  authKey: string;
}

export const getJobs = async (req: IGetJobsRequest): Promise<IJob[]> => {
  const res = await requestWithQuery<IApiListParams, IGetJobs>({
    headers: new Headers({
      authorization: `Basic ${req.authKey}`,
      "content-type": "application/json",
    }),
    method: "GET",
    query: req.query,
    url: `${apiEndpointV1}/jobs`,
  });

  return res.body!.response.list;
};

export interface IGetJobRequest {
  id: UUID;
  authKey: string;
}

export const getJob = async (req: IGetJobRequest): Promise<IJob> => {
  const res = await request<null, IJob>({
    headers: new Headers({
      authorization: `Basic ${req.authKey}`,
      "content-type": "application/json",
    }),
    method: "GET",
    url: `${apiEndpointV1}/jobs/${req.id}`,
  });

  return res.body!.response;
};



