import {
  apiEndpoint,
  gather,
  gatherWithQuery,
  IGatherOptions,
  IGatherQueryOptions,
  IGatherResult,
} from "@app/api";
import { IApiResponse } from "@ilabs/core";

export const apiEndpointV1 = `${apiEndpoint}/v1`;

export const request = async <T, A>(
  opts: IGatherOptions<T>,
): Promise<IGatherResult<IApiResponse<A>>> => {
  return gather<T, IApiResponse<A>>(opts);
};

export const requestWithQuery = async <T, A>(
  opts: IGatherQueryOptions<T>,
): Promise<IGatherResult<IApiResponse<A>>> => {
  return gatherWithQuery<T, IApiResponse<A>>(opts);
};



